import { ref, push, set, get } from 'firebase/database';
import { db } from "utils/firebase";
import { useState } from "react";
import { 
    StyledBackdrop, 
    StyledModal, 
    StyledButtonClose, 
    StyledTitleModal, 
    StyledFormModal,
    StyledInputModal,
    StyledTextArea,
    StyledButtonModal,
} from "./Modal.styled";
import IconClose from "utils/search-svg";
import { Notify } from 'notiflix';

export function Modal({ModalClose}){
    const [firstName, setFirstName] = useState('');
    const [secondName, setSecondName] = useState('');
    const [email, setEmail] = useState('');
    const [number, setNumber] = useState('');
    const [textarea, setTextarea] = useState('');

    function handleInputVisible({target}){
        switch(target.name){
            case 'firstName':
                setFirstName(target.value);
                break;
            case 'secondName':
                setSecondName(target.value);
                break;
            case 'email':
                setEmail(target.value);
                break;
            case 'number':
                setNumber(target.value);
                break;
            case 'textarea':
                setTextarea(target.value);
                break;
            default:
                return;
        }
    };

    function handleFormSubmit(e){
        e.preventDefault()

        if (number.length !== 10) {
            Notify.failure('  !')
            return;
          }
    
        const contact = {
            firstName,
            secondName,
            email,
            number,
            textarea,
        };
        
        const contactRef = push(ref(db, 'contacts'));
        console.log(contactRef);
        set(contactRef, contact)
          .then(() => {
            Notify.success('  ')
            setFirstName('');
            setSecondName('');
            setEmail('');
            setNumber('');
            setTextarea('');
          })
          .catch((error) => {
            Notify.failure(',    !')
            console.error("     : ", error);
          }); 
    };

    //    "contacts"   
const contactsRef = ref(db, 'contacts');

//     
get(contactsRef)
  .then((snapshot) => {
    if (snapshot.exists()) {
      const data = snapshot.val();
    const values = Object.values(data);
    console.log(values);
    } else {
      console.log('  ');
    }
  })
  .catch((error) => {
    console.error('      :', error);
  });

//     
// onValue(contactsRef, (snapshot) => {
//   if (snapshot.exists()) {
//     const data = snapshot.val();
//     // console.log(data);
//   } else {
//     console.log('  ');
//   }
// }, {
//   onlyOnce: true //      
// });

    function handlerModalClose(){
        ModalClose()
    };

    return (
        <StyledBackdrop>
            <StyledModal>
                <StyledButtonClose type="button" onClick={handlerModalClose}>
                    <IconClose />
                </StyledButtonClose>
                <StyledFormModal onSubmit={handleFormSubmit}>
                <StyledTitleModal>  </StyledTitleModal>
                    <StyledInputModal
                        type="text"
                        autoComplete="on"
                        placeholder=" "
                        name="firstName"
                        required
                        onChange={handleInputVisible}
                        value={firstName}
                    />
                    <StyledInputModal
                        type="text"
                        autoComplete="on"
                        placeholder="' "
                        name="secondName"
                        onChange={handleInputVisible}
                        value={secondName}
                    />
                    <StyledInputModal
                        type="email"
                        autoComplete="on"
                        placeholder="Email"
                        name="email"
                        required
                        onChange={handleInputVisible}
                        value={email}
                    />
                    <StyledInputModal
                        type="tel"
                        autoComplete="off"
                        placeholder="0501112223"
                        name="number"
                        required
                        pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
                        title="    050, 096,    7 "
                        onChange={handleInputVisible}
                        value={number}
                    />
                    <StyledTextArea 
                        rows="4" 
                        placeholder="" 
                        name="textarea" 
                        onChange={handleInputVisible}
                        value={textarea}
                    />
                    <StyledButtonModal type="submit" ></StyledButtonModal>
                </StyledFormModal>
            </StyledModal>
        </StyledBackdrop>    
    )
};

